}

.Hero > nav > #Logo {
  aspect-ratio: 181/37;
  height: 37px;
  cursor: pointer;
}

.nav {
  top: 64px;
  display: flex;
  position: fixed;
  z-index: 30;
  width: 100vw;
  justify-content: space-around;
}

@media only screen and (max-width: 2560px) {
  nav.nav {
    justify-content: space-around;
    padding-left: 32px;
    padding-right: 32px;
