  }

  .Hero > nav ul {
    margin-left: 40%;
    margin-top: 16px;
  }

  #SpecsSection > div > div > h3 > span {
    float: left;
  }

  #menuIcon {
    display: inline-block;
    /* margin-left: 80%; */
  }

  .Hero li {
    font-size: 20px;
  }
  #responsiveNav {
    display: flex;
    position: absolute;
    flex-direction: column;
    left: 40%;
    top: 32px;
    max-width: 152px;
    justify-content: center;
    background: linear-gradient(90deg, #3d4483, #00001e);
    padding: 32px 0;
    row-gap: 16px;
    border-radius: 10px;
  }

  .HeroSection2 #spacediv {
    display: none;
  }
  .Hero #bg-circle{
    width: 55.06vw !important;
    height: 61.11vh !important;
  filter: blur(280px) !important;

  }
  #HeroBg{
    object-fit: cover !important;
    aspect-ratio: 1440/291;
    height: 240px;
  }
  
}

@media only screen and (max-width: 500px) {
  nav.nav {
    justify-content: space-around;
    padding-left: 8px;
    padding-right: 8px;
    box-sizing: border-box;
  }
  .Hero > nav ul {
    margin-left: 25% !important;
  }

  .HeroHeadText h1 {
    font-size: 40px !important;
    max-width: 300px !important;
    margin: 0 !important;
  }

  .HeroSubText h2 {
    font-size: 15.44px !important;
    max-width: 220px !important;
    margin: 0 !important;
  }
  .HeroSubText{
    margin-top: 8px !important;
  }
  .mobileView{
    display: inline-block !important;
  }
  .HeroSection2{
    display: none !important;
  }
  .HeroSection1 #HeroNari{
    width: 80vw !important;
    margin-top: 0 !important;
  }
  .HeroSection1{
    justify-content: left !important;
