.tabletView,
.mobileView {
  display: none;
}

.container {
  display: none;
  cursor: pointer;
}

.bar1,
.bar2,
.bar3 {
  background-color: #ffffff;
  margin: 6px 0;
  transition: 0.5s;
}

.bar1 {
  height: 4px;
  width: 16px;
  margin-left: 10px;
}

.bar2 {
  height: 4px;
  width: 30px;
}

.bar3 {
  height: 4px;
  width: 16px;
  margin-left: 5px;
}

.change .bar1 {
  transform: translate(0, 10px) rotate(-45deg);
  height: 4px;
  width: 30px;
  margin-left: 0px;
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: translate(0, -10px) rotate(45deg);
  height: 4px;
  width: 30px;
  margin-left: 0px;
}

