  width: 100%;
  background-color: rgb(0, 0, 30, 0.15);
}

#Gallery > h1 > span {
  color: #6676ff;
}

#Gallery .galleryWrapper {
  position: relative;
  background-color: transparent;
  padding: 152px 152px 0 152px;
  display: flex;
  margin: auto;
  flex-direction: row;
  column-gap: 32px;
  row-gap: 32px;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1440px;
}

.img {
  width: 350px;
  height: 350px;
  object-fit: fill;
  border: 5px solid white;
}

.galleryWrapper > div {
  position: relative;
  max-width: 350px;
  max-height: 350px;
}

.galleryWrapper > div > div {
  background-color: transparent;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  position: absolute;
  z-index: 10;
  top: 0;
  color: transparent;
}

.galleryWrapper > div > div > p {
  padding: 280px 0;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
}

.galleryWrapper > div > div:hover {
  animation: 0.5s img-cover;
  color: white;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.05) 35%,
    rgba(0, 0, 0, 0.15) 50%,
    rgba(0, 0, 0, 0.4) 64%,
    rgba(0, 0, 0, 0.5) 100%
  );
  /* border: 2px solid #6776FF; */
  box-shadow: 0px 0px 0px 100vw #00001e80;
}

.img:hover {
  width: 350px;
  border: 2px solid #6776ff;
  box-shadow: 0px 0px 10px 2px rgb(255, 255, 255, 0.4);
}

/* Success Stories */

#SuccessStories {
  position: relative;
  z-index: 10;
  /* background-color: #00001e; */
  color: white;
}

#SuccessStories h1 {
  font-size: 104.72px;
  font-weight: 700;
  text-align: center;
  margin: 0;
  padding: 192px 0 0 0;
}

#SuccessStories > p {
  font-size: 24.72px;
  color: #b1b9ff;
  font-weight: 500;
  text-align: center;
}

#newsContainer {
  display: flex;
  flex-wrap: wrap;
  column-gap: 32px;
  justify-content: center;
  margin: 152px 0;
}

.newsId {
  position: relative;
  z-index: -2;
  width: 310px;
  background: linear-gradient(90deg, #6776ff, #a500ce);
  border-radius: 5px;
  color: rgb(255, 255, 255);
}

.newsId img {
  max-width: 280px;
  aspect-ratio: 1/1;
  /* position: absolute;
  z-index: 10; */
  margin: 15px 0 0 15px;
}

.newsIdBG {
  background-color: #00001ea7;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  position: absolute;
  z-index: -1;
  top: 2px;
  left: 2px;
  border-radius: 5px;
}

.horizontalFlex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: -5px 15px 15px 15px;
}

.verticalFlex {
  display: flex;
  flex-direction: column;
}

.newsId p.p1 {
  font-size: 15.28px;
  margin: 15px 0 0 0;
  font-weight: 500;
}

.newsId p.p2 {
  font-size: 12px;
  margin: 15px 0 15px 0;
  font-weight: 200;
}

.newsHeading .circle {
  width: 50px;
  height: 50px;
  background-color: #00001e;
  border-radius: 100%;
}

.newsText p {
  color: #b1b9ff;
  font-size: 15.28px;
  max-width: 310px;
  text-align: center;
}

.newsText p span {
  color: white;
  font-weight: 700;
}

@keyframes LineAnimation {
  0% {
    border-bottom: #a500ce solid 2.5px;
  }
  25% {
    border-bottom: #6676ff solid 2.5px;
  }
  50% {
    border-bottom: #a500ce solid 2.5px;
  }
  75% {
    border-bottom: #6676ff solid 2.5px;
  }
  100% {
    border-bottom: #a500ce solid 2.5px;
  }
}

@keyframes img-cover {
  from {
    color: transparent;
    background: transparent;
    box-shadow: 0px 0px 0px 100vw transparent;
  }

  to {
    color: white;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.05) 35%,
      rgba(0, 0, 0, 0.15) 50%,
      rgba(0, 0, 0, 0.4) 64%,
      rgba(0, 0, 0, 0.5) 100%
    );
    box-shadow: 0px 0px 0px 1000vw #00001e80;
  }
}

@keyframes borderLine {
  from {
    width: 0%;
    background-color: #6676ff;
  }
  to {
    width: 100%;
    background-color: #6676ff;
  }
}

@keyframes ContactAnimation {
  from {
    font-weight: 100;
  }
  to {
    font-weight: 400;
  }
}
